/* --------- NAVBAR SECTION ---------- */

.mobile-back-row {
  border-bottom: .05em solid $white;
  border-top: 0;
}

.mobile-menu-items {
  max-height: calc(40vh);
  overflow-y: auto;
}

.card.mobile-nav-group-item {
  background: none;
  padding: 0;

  .card-header {
    padding: 0;
    background: none;
    color: $white;
    border: 0;
    border-top: .05em solid $white;

    button {
      font-size: .85em;
      padding: .5rem 2.7rem;
      margin: 0;
      text-decoration: none;
      width: 100%;
      text-align: left;
      color: $white;
    }
  }

  .card-body {

  }
}
